<template>
  <ListContainer :title="title" @onClick="handleToMore('/video')">
    <VideoList :dataSource="fakeVideo" />
  </ListContainer>
</template>
<script>
import ListContainer from "@/components/ListContainer";
import VideoList from "@/components/VideoList";
import { videos } from "../../fakeData";
import mobileMixin from "../../mixin/mobileMixin";

const fakeVideo = videos.slice(0, 20).map((item) => ({
  ...item,
  path: "/video",
}));

export default {
  mixins: [mobileMixin],
  components: {
    ListContainer,
    VideoList,
  },
  data() {
    return {
      fakeVideo,
    };
  },
  computed: {
    title() {
      const title = this.$route.fullPath.includes("/jp")
        ? "國產視頻"
        : "日韓視頻";
      return `${title} ${this.type}`;
    },
    type() {
      return this.$route.params.type || "";
    },
  },
  watch: {
    isMobile: {
      immediate: true,
      handler(newValue, oldValue) {
        if (newValue && !oldValue) {
          this.handleAddAds();
        } else {
          this.handleRemoveAds();
        }
      },
    },
  },
  methods: {
    handleAddAds() {
      this.fakeVideo = this.addAds(this.fakeVideo, 4);
    },
    handleRemoveAds() {
      this.fakeVideo = this.removeAds(this.fakeVideo);
    },
  },
};
</script>
